.container{width:75%;background:#34353a;position:relative;min-height:100vh;left:25%;padding-bottom:20px}.container .content{display:flex;flex-direction:column;margin:0 60px;opacity:0;animation:fade-in .8s .2s forwards cubic-bezier(0.11, 0, 0.5, 0)}.container .content .map{align-self:center;width:100%;height:400px}.container .content .map iframe{width:100%;height:inherit}.container .content .form{margin-top:10px;display:flex;position:relative;flex-direction:column;gap:10px}.container .content .form .success{position:sticky;padding:10px;max-width:fit-content;background:#494b50;bottom:-20px;color:#fff}.container .content .form .error{color:red}.container .content .form .error::before{display:inline;content:"⚠ "}.container .content .form textarea{border:1px solid rgba(255,255,255,.2);color:#fff;background:#34353a;padding:20px;resize:none}.container .content .form textarea::placeholder{color:#fff}.container .content .form input{border:1px solid rgba(255,255,255,.2);color:#fff;background:#34353a;padding:10px 20px}.container .content .form input::placeholder{color:#fff}.container .content .form button{align-self:start;border:none;width:150px;height:50px;color:#34353a;background:#fff;cursor:pointer;transition:.5s ease-in-out}.container .content .form button:hover{background:#bbb}@keyframes fade-in{100%{opacity:1;filter:blur(0)}}@media(max-width: 913px){.container{width:100%;left:0;padding-bottom:0}.container .content{height:90%;justify-content:center}}@media(max-width: 541px){.container .content .map{height:300px}}@media(max-width: 415px){.container .content .map{height:250px}}